import { writable } from "svelte/store";
const VERSION = "state-v1.0.0";
const defaultState = {
    spoiled: {},
    predictions: {},
};
const storedState = JSON.parse(localStorage.getItem(VERSION));
export const state = writable(storedState || defaultState);
state.subscribe((value) => localStorage.setItem(VERSION, JSON.stringify(value)));
